.autoCompleteItem {
  display: flex;
  color: black;
  list-style: none;
  padding-top: 10px;
  padding-bottom: 10px; }

.autoCompleteItem:hover {
  background-color: blue;
  color: white;
  cursor: pointer; }

#autoCompleteList {
  display: flex;
  flex-direction: column;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
  padding-left: 0px;
  margin-top: 0px;
  background-color: white; }
