#teamView {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin-top: 20px; }

#teamViewDiv {
  display: flex;
  flex-direction: column;
  align-items: left;
  background-color: lightgray;
  height: fit-content;
  width: 100%; }

#teamInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 10px; }
